export default "import * as React from 'react';\r\nimport Skeleton from '@mui/material/Skeleton';\r\nimport Box from '@mui/material/Box';\r\n\r\nexport default function SkeletonColor() {\r\n  return (\r\n    <Box\r\n      sx={{\r\n        bgcolor: '#121212',\r\n        p: 8,\r\n        width: '100%',\r\n        display: 'flex',\r\n        justifyContent: 'center',\r\n      }}\r\n    >\r\n      <Skeleton\r\n        sx={{bgcolor: 'grey.900'}}\r\n        variant='rectangular'\r\n        width={210}\r\n        height={118}\r\n      />\r\n    </Box>\r\n  );\r\n}\r\n";