export default "import * as React from 'react';\r\nimport Box from '@mui/material/Box';\r\nimport Skeleton from '@mui/material/Skeleton';\r\n\r\nexport default function Animations() {\r\n  return (\r\n    <Box sx={{width: 300}}>\r\n      <Skeleton />\r\n      <Skeleton animation='wave' />\r\n      <Skeleton animation={false} />\r\n    </Box>\r\n  );\r\n}\r\n";